"use client"

import type React from "react"
import { LayoutWrapper } from "@/components/layout-wrapper"
import { Search, Plus, Edit2, X } from "lucide-react"
import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { getCurrentUser, checkPermission } from "@/lib/constants"
import { logActivity } from "@/lib/utils"

// Type definitions
type PatientRole = "student" | "teaching_staff" | "non_teaching_staff"
type Program = "CICT" | "CBME"
type StudentCourse = "BSIT" | "BSCS" | "BSIS" | "BTVTED"
type CBMECourse = "BSA" | "BSAIS" | "BPA" | "BSE"
type NonTeachingCategory = "Administrative" | "Security" | "Maintenance" | "Support" | "Clinic"

interface Patient {
  id: string
  firstName: string
  middleName?: string
  lastName: string
  suffix?: string
  dateOfBirth: string
  gender: "Male" | "Female"
  phone: string
  email?: string
  address?: string
  role: PatientRole
  idNumber: string
  program?: Program
  course?: StudentCourse | CBMECourse
  yearLevel?: number
  block?: number
  department?: Program
  staffCategory?: NonTeachingCategory
  // Medical History
  pastIllnesses?: string
  surgeries?: string
  currentMedication?: string
  allergies?: string
  medicalNotes?: string
  // Emergency Contacts
  primaryContactName?: string
  primaryContactRelationship?: string
  primaryContactPhone?: string
  primaryContactAddress?: string
  secondaryContactName?: string
  secondaryContactRelationship?: string
  secondaryContactPhone?: string
  secondaryContactAddress?: string
  attachments?: string
}

interface FormData {
  firstName: string
  middleName: string
  lastName: string
  suffix: string
  dateOfBirth: string
  gender: "Male" | "Female"
  phone: string
  email: string
  address: string
  role: PatientRole
  idNumber: string
  program: Program | ""
  course: string
  yearLevel: number | ""
  block: number | ""
  department: Program | ""
  staffCategory: NonTeachingCategory | ""
  // Medical History
  pastIllnesses: string
  surgeries: string
  currentMedication: string
  allergies: string
  medicalNotes: string
  // Emergency Contacts
  primaryContactName: string
  primaryContactRelationship: string
  primaryContactPhone: string
  primaryContactAddress: string
  secondaryContactName: string
  secondaryContactRelationship: string
  secondaryContactPhone: string
  secondaryContactAddress: string
}

const COURSE_OPTIONS: Record<Program, string[]> = {
  CICT: ["BSIT", "BSCS", "BSIS", "BTVTED"],
  CBME: ["BSA", "BSAIS", "BPA", "BSE"],
}

const getRoleDisplay = (role: PatientRole): string => {
  switch (role) {
    case "student":
      return "student"
    case "teaching_staff":
      return "teaching staff"
    case "non_teaching_staff":
      return "non-teaching staff"
  }
}

export default function PatientsPage() {
  const router = useRouter()
  const [patients, setPatients] = useState<Patient[]>([])
  const [searchTerm, setSearchTerm] = useState("")
  const [roleFilter, setRoleFilter] = useState("all")
  const [sortOption, setSortOption] = useState("name_az")
  const [showAddForm, setShowAddForm] = useState(false)
  const [editingId, setEditingId] = useState<string | null>(null)
  const [formErrors, setFormErrors] = useState<string[]>([])
  const [user, setUser] = useState<any>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [availableCourses, setAvailableCourses] = useState<string[]>([])

  const [formData, setFormData] = useState<FormData>({
    firstName: "",
    middleName: "",
    lastName: "",
    suffix: "",
    dateOfBirth: "",
    gender: "Male",
    phone: "",
    email: "",
    address: "",
    role: "student",
    idNumber: "",
    program: "CICT",
    course: "",
    yearLevel: "",
    block: "",
    department: "",
    staffCategory: "",
    // Medical History
    pastIllnesses: "",
    surgeries: "",
    currentMedication: "",
    allergies: "",
    medicalNotes: "",
    // Emergency Contacts
    primaryContactName: "",
    primaryContactRelationship: "",
    primaryContactPhone: "",
    primaryContactAddress: "",
    secondaryContactName: "",
    secondaryContactRelationship: "",
    secondaryContactPhone: "",
    secondaryContactAddress: "",
  })

  useEffect(() => {
    const currentUser = getCurrentUser()
    if (currentUser) setUser(currentUser)
  }, [])

  useEffect(() => {
    if (user) fetchPatients()
  }, [user])

  useEffect(() => {
    // Update available courses when program changes
    if (formData.program && formData.program in COURSE_OPTIONS) {
      setAvailableCourses(COURSE_OPTIONS[formData.program])
      // Reset course if it's not available in the new program
      if (!COURSE_OPTIONS[formData.program].includes(formData.course)) {
        setFormData((prev) => ({ ...prev, course: "" }))
      }
    }
  }, [formData.program])

  const fetchPatients = async () => {
    if (!user) return
    try {
      setIsLoading(true)
      const response = await fetch("/api/patients", {
        headers: { "x-user-id": user.id },
      })
      if (response.ok) {
        const data = await response.json()
        setPatients(data.patients || [])
      }
    } catch (error) {
      console.error("Error fetching patients:", error)
    } finally {
      setIsLoading(false)
    }
  }

  const canManagePatients = user && checkPermission(user.role, "canManagePatients")
  const canEditPatients = user && checkPermission(user.role, "canEditPatients")

  const getFullName = (patient: Patient): string => {
    const name = [patient.firstName, patient.middleName, patient.lastName]
      .filter(Boolean)
      .join(" ")
    return patient.suffix ? `${name} ${patient.suffix}` : name
  }

  const getDisplayName = (patient: Patient): string => {
    const middleInitial = patient.middleName ? `${patient.middleName.charAt(0)}. ` : ""
    const suffix = patient.suffix ? ` ${patient.suffix}` : ""
    return `${patient.lastName}, ${patient.firstName} ${middleInitial}${suffix}`.trim()
  }

  const getCourseDisplay = (patient: Patient): string => {
    if (patient.role !== "student" || !patient.course || !patient.yearLevel) return ""
    
    const courseInitials = patient.course.match(/[A-Z]/g)?.join("") || patient.course
    const block = patient.block ? ` - ${patient.block}` : ""
    return `${courseInitials} ${patient.yearLevel}${block}`
  }

  const filteredAndSortedPatients = patients
    .filter((p) =>
      getFullName(p).toLowerCase().includes(searchTerm.toLowerCase()) ||
      p.idNumber.toLowerCase().includes(searchTerm.toLowerCase())
    )
    .filter((p) => roleFilter === "all" || p.role === roleFilter)
    .sort((a, b) => {
      if (sortOption === "name_az") {
        return getFullName(a).localeCompare(getFullName(b))
      } else if (sortOption === "name_za") {
        return getFullName(b).localeCompare(getFullName(a))
      } else if (sortOption === "id_asc") {
        return a.idNumber.localeCompare(b.idNumber)
      } else if (sortOption === "id_desc") {
        return b.idNumber.localeCompare(a.idNumber)
      }
      return 0
    })

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleAddPatient = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!canManagePatients || isSubmitting) return

    setIsSubmitting(true)

    const errors: string[] = []

    // Validate name fields (only letters)
    if (!formData.firstName.trim()) {
      errors.push("First name is required")
    } else if (!/^[a-zA-Z\s]*$/.test(formData.firstName)) {
      errors.push("First name can only contain letters")
    }

    if (formData.middleName && !/^[a-zA-Z\s]*$/.test(formData.middleName)) {
      errors.push("Middle name can only contain letters")
    }

    if (!formData.lastName.trim()) {
      errors.push("Last name is required")
    } else if (!/^[a-zA-Z\s]*$/.test(formData.lastName)) {
      errors.push("Last name can only contain letters")
    }

    if (formData.suffix && !/^[a-zA-Z\s]*$/.test(formData.suffix)) {
      errors.push("Suffix can only contain letters")
    }

    if (!formData.dateOfBirth) {
      errors.push("Date of birth is required")
    }

    if (!formData.phone) {
      errors.push("Contact number is required")
    } else if (!/^09\d{9}$/.test(formData.phone)) {
      errors.push("Phone must start with 09 and be exactly 11 digits")
    }

    if (!formData.idNumber.trim()) {
      errors.push("ID number is required")
    } else if (!/^[a-zA-Z0-9-]+$/.test(formData.idNumber)) {
      errors.push("ID number can only contain letters, numbers, and dashes")
    }

    // Validate role-specific fields
    if (formData.role === "student") {
      if (!formData.program) {
        errors.push("Program is required for students")
      }
      if (!formData.course) {
        errors.push("Course is required for students")
      }
      if (!formData.yearLevel) {
        errors.push("Year level is required for students")
      }
      if (!formData.block) {
        errors.push("Block is required for students")
      }
    }

    if (formData.role === "teaching_staff") {
      if (!formData.department) {
        errors.push("Program is required for teaching staff")
      }
    }

    if (formData.role === "non_teaching_staff") {
      if (!formData.staffCategory) {
        errors.push("Category is required for non-teaching staff")
      }
    }

    // Validate primary emergency contact (required)
    if (!formData.primaryContactName.trim()) {
      errors.push("Primary contact name is required")
    }
    if (!formData.primaryContactRelationship.trim()) {
      errors.push("Primary contact relationship is required")
    }
    if (!formData.primaryContactPhone.trim()) {
      errors.push("Primary contact phone number is required")
    } else if (!/^09\d{9}$/.test(formData.primaryContactPhone)) {
      errors.push("Primary contact phone must start with 09 and be exactly 11 digits")
    }

    if (errors.length > 0) {
      setFormErrors(errors)
      return
    }

    try {
      const payload = {
        firstName: formData.firstName,
        middleName: formData.middleName || undefined,
        lastName: formData.lastName,
        suffix: formData.suffix || undefined,
        dateOfBirth: formData.dateOfBirth,
        gender: formData.gender,
        phone: formData.phone,
        email: formData.email || undefined,
        address: formData.address || undefined,
        role: formData.role,
        idNumber: formData.idNumber,
        ...(formData.role === "student" && {
          program: formData.program,
          course: formData.course,
          yearLevel: parseInt(formData.yearLevel as unknown as string),
          block: parseInt(formData.block as unknown as string),
        }),
        ...(formData.role === "teaching_staff" && {
          department: formData.department,
        }),
        ...(formData.role === "non_teaching_staff" && {
          staffCategory: formData.staffCategory,
        }),
        // Medical History
        pastIllnesses: formData.pastIllnesses || undefined,
        surgeries: formData.surgeries || undefined,
        currentMedication: formData.currentMedication || undefined,
        allergies: formData.allergies || undefined,
        medicalNotes: formData.medicalNotes || undefined,
        // Emergency Contacts
        primaryContactName: formData.primaryContactName || undefined,
        primaryContactRelationship: formData.primaryContactRelationship || undefined,
        primaryContactPhone: formData.primaryContactPhone || undefined,
        primaryContactAddress: formData.primaryContactAddress || undefined,
        secondaryContactName: formData.secondaryContactName || undefined,
        secondaryContactRelationship: formData.secondaryContactRelationship || undefined,
        secondaryContactPhone: formData.secondaryContactPhone || undefined,
        secondaryContactAddress: formData.secondaryContactAddress || undefined,
      }

      const method = editingId ? "PUT" : "POST"
      const body = editingId ? { id: editingId, ...payload } : payload

      const response = await fetch("/api/patients", {
        method,
        headers: { "Content-Type": "application/json", "x-user-id": user.id },
        body: JSON.stringify(body),
      })

      const result = await response.json()

      if (response.ok) {
        logActivity(
          "patient",
          editingId
            ? `Updated patient: ${getFullName(result.patient)}`
            : `Added new patient: ${getFullName(result.patient)}`
        )
        await fetchPatients()
        handleCancelEdit()
      } else if (result.errors) {
        setFormErrors(result.errors)
      } else {
        setFormErrors(["Failed to save patient. Please try again."])
      }
    } catch (error) {
      console.error("Error saving patient:", error)
      setFormErrors(["Failed to save patient. Please try again."])
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleEditPatient = (patient: Patient) => {
    if (!canEditPatients) return
    setEditingId(patient.id)
    setFormData({
      firstName: patient.firstName,
      middleName: patient.middleName || "",
      lastName: patient.lastName,
      suffix: patient.suffix || "",
      dateOfBirth: patient.dateOfBirth,
      gender: patient.gender,
      phone: patient.phone,
      email: patient.email || "",
      address: patient.address || "",
      role: patient.role,
      idNumber: patient.idNumber,
      program: patient.program || "CICT",
      course: patient.course || "",
      yearLevel: patient.yearLevel || "",
      block: patient.block || "",
      department: patient.department || "",
      staffCategory: patient.staffCategory || "",
      // Medical History
      pastIllnesses: patient.pastIllnesses || "",
      surgeries: patient.surgeries || "",
      currentMedication: patient.currentMedication || "",
      allergies: patient.allergies || "",
      medicalNotes: patient.medicalNotes || "",
      // Emergency Contacts
      primaryContactName: patient.primaryContactName || "",
      primaryContactRelationship: patient.primaryContactRelationship || "",
      primaryContactPhone: patient.primaryContactPhone || "",
      primaryContactAddress: patient.primaryContactAddress || "",
      secondaryContactName: patient.secondaryContactName || "",
      secondaryContactRelationship: patient.secondaryContactRelationship || "",
      secondaryContactPhone: patient.secondaryContactPhone || "",
      secondaryContactAddress: patient.secondaryContactAddress || "",
    })
    if (patient.program) {
      setAvailableCourses(COURSE_OPTIONS[patient.program])
    }
    setShowAddForm(true)
    setFormErrors([])
  }

  const handleCancelEdit = () => {
    setEditingId(null)
    setFormData({
      firstName: "",
      middleName: "",
      lastName: "",
      suffix: "",
      dateOfBirth: "",
      gender: "Male",
      phone: "",
      email: "",
      address: "",
      role: "student",
      idNumber: "",
      program: "CICT",
      course: "",
      yearLevel: "",
      block: "",
      department: "",
      staffCategory: "",
      // Medical History
      pastIllnesses: "",
      surgeries: "",
      currentMedication: "",
      allergies: "",
      medicalNotes: "",
      // Emergency Contacts
      primaryContactName: "",
      primaryContactRelationship: "",
      primaryContactPhone: "",
      primaryContactAddress: "",
      secondaryContactName: "",
      secondaryContactRelationship: "",
      secondaryContactPhone: "",
      secondaryContactAddress: "",
    })
    setAvailableCourses(COURSE_OPTIONS.CICT)
    setFormErrors([])
    setShowAddForm(false)
  }

  const handleDeletePatient = async (id: string) => {
    if (!confirm("Are you sure you want to delete this patient?")) return
    try {
      const response = await fetch("/api/patients", {
        method: "DELETE",
        headers: { "Content-Type": "application/json", "x-user-id": user.id },
        body: JSON.stringify({ id }),
      })
      if (response.ok) await fetchPatients()
    } catch (error) {
      console.error("Error deleting patient:", error)
    }
  }

  return (
    <LayoutWrapper>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-[#8B3A3A]">Patients</h1>
            <p className="text-gray-600">Manage school community member records</p>
          </div>
          {canManagePatients && (
            <button
              onClick={() => {
                setShowAddForm(true)
                setEditingId(null)
                setFormData({
                  firstName: "",
                  middleName: "",
                  lastName: "",
                  suffix: "",
                  dateOfBirth: "",
                  gender: "Male",
                  phone: "",
                  email: "",
                  address: "",
                  role: "student",
                  idNumber: "",
                  program: "CICT",
                  course: "",
                  yearLevel: "",
                  block: "",
                  department: "",
                  staffCategory: "",
                  // Medical History
                  pastIllnesses: "",
                  surgeries: "",
                  currentMedication: "",
                  allergies: "",
                  medicalNotes: "",
                  // Emergency Contacts
                  primaryContactName: "",
                  primaryContactRelationship: "",
                  primaryContactPhone: "",
                  primaryContactAddress: "",
                  secondaryContactName: "",
                  secondaryContactRelationship: "",
                  secondaryContactPhone: "",
                  secondaryContactAddress: "",
                })
                setAvailableCourses(COURSE_OPTIONS.CICT)
              }}
              className="bg-[#8B3A3A] text-white px-6 py-3 rounded-lg font-semibold hover:bg-[#6D2E2E] flex items-center gap-2"
            >
              <Plus size={20} /> Add Patient
            </button>
          )}
        </div>

        {/* Search Bar */}
        <div className="bg-[#8B3A3A] rounded-xl p-6">
          <div className="flex items-center gap-4">
            <div className="flex-1 relative">
              <Search className="absolute left-4 top-3 text-white opacity-60" size={20} />
              <input
                type="text"
                placeholder="Search by name or ID number..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-12 pr-4 py-2 bg-white/20 text-white placeholder-white/60 rounded-lg focus:outline-none focus:ring-2 focus:ring-white"
              />
            </div>

            <select
              value={roleFilter}
              onChange={(e) => setRoleFilter(e.target.value)}
              className="bg-white text-[#8B3A3A] px-3 py-2 rounded-lg focus:outline-none focus:ring-2 focus:ring-white font-medium cursor-pointer text-sm"
            >
              <option value="all">All Patients</option>
              <option value="student">Student</option>
              <option value="teaching_staff">Teaching Staff</option>
              <option value="non_teaching_staff">Non-teaching Staff</option>
            </select>

            <select
              value={sortOption}
              onChange={(e) => setSortOption(e.target.value)}
              className="bg-white text-[#8B3A3A] px-3 py-2 rounded-lg focus:outline-none focus:ring-2 focus:ring-white font-medium cursor-pointer text-sm"
            >
              <option value="name_az">Name: A-Z</option>
              <option value="name_za">Name: Z-A</option>
              <option value="id_asc">ID No: Ascending</option>
              <option value="id_desc">ID No: Descending</option>
            </select>
          </div>
        </div>

        {/* Add / Edit Form */}
        {showAddForm && canManagePatients && (
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold">{editingId ? "Edit Patient" : "Add New Patient"}</h2>
              <button onClick={handleCancelEdit} className="text-gray-500 hover:text-gray-700">
                <X size={24} />
              </button>
            </div>

            {formErrors.length > 0 && (
              <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
                <h3 className="font-semibold text-red-800 mb-2">Please fix the following errors:</h3>
                <ul className="list-disc list-inside space-y-1">
                  {formErrors.map((error, i) => (
                    <li key={i} className="text-red-600 text-sm">
                      {error}
                    </li>
                  ))}
                </ul>
              </div>
            )}

            <form onSubmit={handleAddPatient} className="space-y-8">
              {/* PERSONAL INFORMATION SECTION */}
              <div>
                <h3 className="text-lg font-semibold text-gray-800 mb-4 pb-2 border-b-2 border-[#8B3A3A]">
                  Personal Information
                </h3>

                {/* Name Fields */}
                <div className="grid grid-cols-2 gap-4 mb-4">
                  <div>
                    <label className="block text-sm font-semibold mb-2">
                      First Name <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      name="firstName"
                      value={formData.firstName}
                      onChange={handleInputChange}
                      placeholder="Letters only"
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-semibold mb-2">Middle Name (Optional)</label>
                    <input
                      type="text"
                      name="middleName"
                      value={formData.middleName}
                      onChange={handleInputChange}
                      placeholder="Letters only"
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4 mb-4">
                  <div>
                    <label className="block text-sm font-semibold mb-2">
                      Last Name <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      name="lastName"
                      value={formData.lastName}
                      onChange={handleInputChange}
                      placeholder="Letters only"
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-semibold mb-2">Suffix (Optional)</label>
                    <input
                      type="text"
                      name="suffix"
                      value={formData.suffix}
                      onChange={handleInputChange}
                      placeholder="e.g., Jr., Sr."
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                </div>

                {/* Gender & Date of Birth */}
                <div className="grid grid-cols-2 gap-4 mb-4">
                  <div>
                    <label className="block text-sm font-semibold mb-2">
                      Gender <span className="text-red-500">*</span>
                    </label>
                    <select
                      name="gender"
                      value={formData.gender}
                      onChange={handleInputChange}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    >
                      <option value="Male">Male</option>
                      <option value="Female">Female</option>
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-semibold mb-2">
                      Date of Birth <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="date"
                      name="dateOfBirth"
                      value={formData.dateOfBirth}
                      onChange={handleInputChange}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                </div>

                {/* Contact, Email & Address */}
                <div className="grid grid-cols-3 gap-4 mb-4">
                  <div>
                    <label className="block text-sm font-semibold mb-2">
                      Contact Number <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      name="phone"
                      value={formData.phone}
                      onChange={handleInputChange}
                      placeholder="09xxxxxxxxx"
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-semibold mb-2">Email (Optional)</label>
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      placeholder="email@example.com"
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-semibold mb-2">Address (Optional)</label>
                    <input
                      type="text"
                      name="address"
                      value={formData.address}
                      onChange={handleInputChange}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                </div>
              </div>

              {/* SCHOOL INFORMATION SECTION */}
              <div>
                <h3 className="text-lg font-semibold text-gray-800 mb-4 pb-2 border-b-2 border-[#8B3A3A]">
                  School Information
                </h3>

                {/* Role */}
                <div className="mb-4">
                  <label className="block text-sm font-semibold mb-2">
                    Role / Classification <span className="text-red-500">*</span>
                  </label>
                  <select
                    name="role"
                    value={formData.role}
                    onChange={handleInputChange}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                  >
                    <option value="student">student</option>
                    <option value="teaching_staff">teaching staff</option>
                    <option value="non_teaching_staff">non-teaching staff</option>
                  </select>
                </div>

                {/* ID Number */}
                <div className="mb-4">
                  <label className="block text-sm font-semibold mb-2">
                    ID Number <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    name="idNumber"
                    value={formData.idNumber}
                    onChange={handleInputChange}
                    placeholder="Letters, numbers, and dash (-) only"
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                  />
                </div>

                {/* STUDENT SPECIFIC FIELDS */}
                {formData.role === "student" && (
                  <div className="bg-blue-50 p-4 rounded-lg space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-semibold mb-2">
                          Program <span className="text-red-500">*</span>
                        </label>
                        <select
                          name="program"
                          value={formData.program}
                          onChange={handleInputChange}
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        >
                          <option value="">Select Program</option>
                          <option value="CICT">CICT</option>
                          <option value="CBME">CBME</option>
                        </select>
                      </div>
                      <div>
                        <label className="block text-sm font-semibold mb-2">
                          Course <span className="text-red-500">*</span>
                        </label>
                        <select
                          name="course"
                          value={formData.course}
                          onChange={handleInputChange}
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        >
                          <option value="">Select Course</option>
                          {availableCourses.map((c) => (
                            <option key={c} value={c}>
                              {c}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-semibold mb-2">
                          Year Level <span className="text-red-500">*</span>
                        </label>
                        <select
                          name="yearLevel"
                          value={formData.yearLevel}
                          onChange={handleInputChange}
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        >
                          <option value="">Select Year</option>
                          <option value="1">1st Year</option>
                          <option value="2">2nd Year</option>
                          <option value="3">3rd Year</option>
                          <option value="4">4th Year</option>
                        </select>
                      </div>
                      <div>
                        <label className="block text-sm font-semibold mb-2">
                          Block <span className="text-red-500">*</span>
                        </label>
                        <select
                          name="block"
                          value={formData.block}
                          onChange={handleInputChange}
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        >
                          <option value="">Select Block</option>
                          <option value="1">Block 1</option>
                          <option value="2">Block 2</option>
                          <option value="3">Block 3</option>
                          <option value="4">Block 4</option>
                          <option value="5">Block 5</option>
                        </select>
                      </div>
                    </div>
                  </div>
                )}

                {/* TEACHING STAFF SPECIFIC FIELDS */}
                {formData.role === "teaching_staff" && (
                  <div className="bg-green-50 p-4 rounded-lg">
                    <div>
                      <label className="block text-sm font-semibold mb-2">
                        Program <span className="text-red-500">*</span>
                      </label>
                      <select
                        name="department"
                        value={formData.department}
                        onChange={handleInputChange}
                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                      >
                        <option value="">Select Program</option>
                        <option value="CICT">CICT</option>
                        <option value="CBME">CBME</option>
                      </select>
                    </div>
                  </div>
                )}

                {/* NON-TEACHING STAFF SPECIFIC FIELDS */}
                {formData.role === "non_teaching_staff" && (
                  <div className="bg-yellow-50 p-4 rounded-lg">
                    <div>
                      <label className="block text-sm font-semibold mb-2">
                        Category <span className="text-red-500">*</span>
                      </label>
                      <select
                        name="staffCategory"
                        value={formData.staffCategory}
                        onChange={handleInputChange}
                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                      >
                        <option value="">Select Category</option>
                        <option value="Administrative">Administrative</option>
                        <option value="Security">Security</option>
                        <option value="Maintenance">Maintenance</option>
                        <option value="Support">Support</option>
                        <option value="Clinic">Clinic</option>
                      </select>
                    </div>
                  </div>
                )}
              </div>

              {/* MEDICAL HISTORY SECTION */}
              <div>
                <h3 className="text-lg font-semibold text-gray-800 mb-4 pb-2 border-b-2 border-[#8B3A3A]">
                  Medical History
                </h3>

                <div className="grid grid-cols-2 gap-4 mb-4">
                  <div>
                    <label className="block text-sm font-semibold mb-2">Past Illnesses</label>
                    <textarea
                      name="pastIllnesses"
                      value={formData.pastIllnesses}
                      onChange={handleInputChange}
                      placeholder="List any past illnesses..."
                      rows={3}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-semibold mb-2">Surgeries</label>
                    <textarea
                      name="surgeries"
                      value={formData.surgeries}
                      onChange={handleInputChange}
                      placeholder="List any past surgeries..."
                      rows={3}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4 mb-4">
                  <div>
                    <label className="block text-sm font-semibold mb-2">Current Medication</label>
                    <textarea
                      name="currentMedication"
                      value={formData.currentMedication}
                      onChange={handleInputChange}
                      placeholder="List current medications..."
                      rows={3}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-semibold mb-2">Allergies</label>
                    <textarea
                      name="allergies"
                      value={formData.allergies}
                      onChange={handleInputChange}
                      placeholder="List any allergies..."
                      rows={3}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                    />
                  </div>
                </div>

                <div className="mb-4">
                  <label className="block text-sm font-semibold mb-2">Additional Notes</label>
                  <textarea
                    name="medicalNotes"
                    value={formData.medicalNotes}
                    onChange={handleInputChange}
                    placeholder="Any additional medical notes..."
                    rows={3}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                  />
                </div>
              </div>

              {/* EMERGENCY CONTACTS SECTION */}
              <div>
                <h3 className="text-lg font-semibold text-gray-800 mb-4 pb-2 border-b-2 border-[#8B3A3A]">
                  Emergency Contacts
                </h3>

                {/* Primary Contact */}
                <div className="mb-6">
                  <h4 className="text-md font-semibold text-gray-700 mb-3">Primary Contact <span className="text-red-500">*</span></h4>
                  <div className="bg-red-50 p-4 rounded-lg space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-semibold mb-2">Full Name <span className="text-red-500">*</span></label>
                        <input
                          type="text"
                          name="primaryContactName"
                          value={formData.primaryContactName}
                          onChange={handleInputChange}
                          placeholder="Full name"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-semibold mb-2">Relationship <span className="text-red-500">*</span></label>
                        <input
                          type="text"
                          name="primaryContactRelationship"
                          value={formData.primaryContactRelationship}
                          onChange={handleInputChange}
                          placeholder="e.g., Parent, Spouse, Sibling"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        />
                      </div>
                    </div>
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-semibold mb-2">Contact Number <span className="text-red-500">*</span></label>
                        <input
                          type="text"
                          name="primaryContactPhone"
                          value={formData.primaryContactPhone}
                          onChange={handleInputChange}
                          placeholder="09xxxxxxxxx"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-semibold mb-2">Address</label>
                        <input
                          type="text"
                          name="primaryContactAddress"
                          value={formData.primaryContactAddress}
                          onChange={handleInputChange}
                          placeholder="Address"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        />
                      </div>
                    </div>
                  </div>
                </div>

                {/* Secondary Contact */}
                <div className="mb-6">
                  <h4 className="text-md font-semibold text-gray-700 mb-3">Secondary Contact (Optional)</h4>
                  <div className="bg-gray-50 p-4 rounded-lg space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-semibold mb-2">Full Name</label>
                        <input
                          type="text"
                          name="secondaryContactName"
                          value={formData.secondaryContactName}
                          onChange={handleInputChange}
                          placeholder="Full name"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-semibold mb-2">Relationship</label>
                        <input
                          type="text"
                          name="secondaryContactRelationship"
                          value={formData.secondaryContactRelationship}
                          onChange={handleInputChange}
                          placeholder="e.g., Parent, Spouse, Sibling"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        />
                      </div>
                    </div>
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-semibold mb-2">Contact Number</label>
                        <input
                          type="text"
                          name="secondaryContactPhone"
                          value={formData.secondaryContactPhone}
                          onChange={handleInputChange}
                          placeholder="09xxxxxxxxx"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-semibold mb-2">Address</label>
                        <input
                          type="text"
                          name="secondaryContactAddress"
                          value={formData.secondaryContactAddress}
                          onChange={handleInputChange}
                          placeholder="Address"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              {/* FILE ATTACHMENT SECTION */}
              <div>
                <h3 className="text-lg font-semibold text-gray-800 mb-4 pb-2 border-b-2 border-[#8B3A3A]">
                  File Attachments
                </h3>
                <div className="mb-4">
                  <label className="block text-sm font-semibold mb-2">Upload Files</label>
                  <input
                    type="file"
                    multiple
                    accept=".pdf,.doc,.docx,.jpg,.jpeg,.png"
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
                  />
                  <p className="text-xs text-gray-500 mt-1">Accepted formats: PDF, DOC, DOCX, JPG, JPEG, PNG</p>
                </div>
              </div>

              {/* Form Actions */}
              <div className="flex gap-4 pt-4 border-t border-gray-200">
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className="bg-[#8B3A3A] text-white px-8 py-2 rounded-lg font-semibold hover:bg-[#6D2E2E] transition disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  {isSubmitting ? "Saving..." : editingId ? "Update Patient" : "Add Patient"}
                </button>
                <button
                  type="button"
                  onClick={handleCancelEdit}
                  className="bg-gray-200 text-gray-900 px-8 py-2 rounded-lg font-semibold hover:bg-gray-300 transition"
                >
                  Cancel
                </button>
              </div>
            </form>
          </div>
        )}

        {/* Patient List */}
        <div className="space-y-4">
          {isLoading ? (
            <div className="text-center py-12">
              <p className="text-gray-500">Loading patients...</p>
            </div>
          ) : filteredAndSortedPatients.length > 0 ? (
            filteredAndSortedPatients.map((patient) => (
              <div
                key={patient.id}
                onClick={() => router.push(`/patients/${patient.id}`)}
                className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 hover:shadow-md transition cursor-pointer"
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <h3 className="text-xl font-bold text-gray-800">{getDisplayName(patient)}</h3>
                    <div className="mt-3 text-sm text-gray-600">
                      <p><strong>ID:</strong> {patient.idNumber} | <strong>Phone:</strong> {patient.phone} | <strong>Course:</strong> {patient.role === "student" && patient.course ? getCourseDisplay(patient) : "N/A"}</p>
                    </div>
                  </div>
                </div>
              </div>
            ))
          ) : (
            <div className="text-center py-12">
              <p className="text-gray-500">No patients found</p>
            </div>
          )}
        </div>
      </div>
    </LayoutWrapper>
  )
}
